import type RemoteInputElement from '@github/remote-input-element'
import {changeValue} from '@github-ui/form-utils'
import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {announce} from '@github-ui/aria-live'

on('click', '[data-workflow-editor-sidebar-url]', async function (event) {
  const link = event.currentTarget
  const url = link.getAttribute('data-workflow-editor-sidebar-url')!
  const actionContent = document.querySelector<HTMLElement>('#js-workflow-editor-sidebar-content')!
  actionContent.textContent = ''
  const remoteInput = document.querySelector<RemoteInputElement>('.js-remoteinput')!
  remoteInput.hidden = true

  const html = await fetchSafeDocumentFragment(document, url)
  actionContent.append(html)
})

on('click', '[data-workflow-editor-sidebar-search-url]', function (event) {
  const link = event.currentTarget
  const searchUrl = link.getAttribute('data-workflow-editor-sidebar-search-url')!

  const remoteInput = document.querySelector<RemoteInputElement>('.js-remoteinput')!
  remoteInput.src = searchUrl

  const input = remoteInput.input
  if (!(input instanceof HTMLInputElement)) return
  input.placeholder = link.getAttribute('data-workflow-editor-sidebar-search-placeholder')!

  const searchQuery = link.getAttribute('data-workflow-editor-sidebar-search-query')!
  changeValue(input, searchQuery)
})

on('remote-input-success', '.js-remoteinput', ({currentTarget}) => {
  if (currentTarget instanceof HTMLElement) currentTarget.hidden = false
  const results = document.querySelectorAll<HTMLElement>('.js-marketplace-action-search-item')
  if (results.length === 0) {
    announce('0 results found')
  } else {
    announce(`${results.length} results found`)
  }
})

on('click', '.js-action-version-dropdown button', ({currentTarget}) => {
  const selectedTagName = currentTarget.getAttribute('value')!
  const action = currentTarget.closest<HTMLElement>('.js-action')!
  for (const selected of action.querySelectorAll('.js-action-version-selected')) {
    selected.textContent = selectedTagName
  }
  for (const example of action.querySelectorAll<HTMLElement>('.js-action-release-example')) {
    const tagName = example.getAttribute('data-tag-name')!
    const selected = tagName === selectedTagName
    example.hidden = !selected
    if (selected) {
      example.setAttribute('id', example.getAttribute('data-id')!)
    } else {
      example.removeAttribute('id')
    }
  }
})
